
.signup-form-container {
  width: 80%; /* Set a percentage width for responsiveness */
  max-width: 400px; /* Set a maximum width to prevent the form from becoming too wide */
  margin: 50px auto;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  text-align: center;
    border: 3px solid gray;
}
.signup-form-container:hover{
  box-shadow:  rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset, rgba(0, 0, 0, 0.15) 0px -36px 30px 0px inset, rgba(0, 0, 0, 0.1) 0px -79px 40px 0px inset, rgba(0, 0, 0, 0.06) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;;
   border-radius: 20px 30px 20px 30px;
  border: 5px solid green;
  /* box-shadow:  0 0 5px green,
    0 0 10px green,
    0 0 20px green,
    0 0 40px green,
    0 0 80px green,
    0 0 160px rgba(0, 0, 0, 0.6);
  background-image: linear-gradient(black, green); */
