.footer {
  background-color: #222;
  color: #fff;
  padding: 20px 0;
  text-align: center;
}

.footer-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-start;
}

.footer-section {
  flex: 1;
  min-width: 200px;
  margin: 10px;
}

.footer-section h1,
.footer-section h2 {
  color: #f9d342;
  margin-bottom: 10px;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section ul li {
  margin: 5px 0;
}

.footer-section ul li a {
  color: #fff;
  text-decoration: none;
}

.footer-section p {
  margin: 5px 0;
}

.footer-section form {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.footer-section input[type="email"] {
  padding: 10px;
  border: none;
  border-radius: 5px 0 0 5px;
  outline: none;
  width: 70%;
  font-size: 16px;
  color: #222;
}

.footer-section input[type="email"]::placeholder {
  color: #aaa;
}

.footer-section button {
  padding: 10px;
  border: none;
  background-color: #f9d342;
  color: #222;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  outline: none;
  font-size: 16px;
}

.footer-section button:hover {
  background-color: #ffc107;
}
