.reset-password-container {
  max-width: 400px;
  margin: 30 auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.reset-password-container h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

.reset-password-form .form-group {
  margin-bottom: 20px;
}

.reset-password-form label {
  display: block;
  font-weight: bold;
}

.reset-password-form input[type="email"],
.reset-password-form input[type="text"],
.reset-password-form input[type="password"] {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.reset-password-form button[type="button"],
.reset-password-form button[type="submit"] {
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.reset-password-form button[type="button"]:hover,
.reset-password-form button[type="submit"]:hover {
  background-color: #0056b3;
}

.error-message {
  color: red;
  margin-top: 10px;
}
.reset-password-container:hover{
  border: 2px solid brown;
}