/* categorybuttoncss */

.dropdown-content {
  display: none;
  font-size: 13px;
  background-color: #222;
  position: absolute;
  z-index: 1;
  min-width: 200px;

  border-radius: 0px 15px 15px 15px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
}

.dropdown-content a {
  color: white;
  padding: 8px 10px;
  text-decoration: none;
  display: block;
  transition: 0.1s;
}


.dropdown-content a:focus {
  background-color: #212121;
}

.dropdown-content #top:hover {
  border-radius: 0px 13px 0px 0px;
}

.dropdown-content #bottom:hover {
  border-radius: 0px 0px 13px 13px;
}

.paste-button:hover button {
  border-radius: 15px 15px 0px 0px;
}

.paste-button:hover .dropdown-content {
  display: block;
}

/* morebuttoncss */
.button2 {
  width: 140px;
  height: 40px;
  cursor: pointer;

  transition: all 0.2s ease-in;
  position: relative;
  overflow: hidden;
  z-index: 1;

  font-size: 18px;
  border-radius: 0.5em;
  background: #e8e8e8;
  border: 1px solid #e8e8e8;
  box-shadow: 6px 6px 12px #c5c5c5, -6px -6px 12px #ffffff;
}

.button2:active {
  color: #666;
  box-shadow: inset 4px 4px 12px #c5c5c5, inset -4px -4px 12px #ffffff;
}

.button2:after {
  content: "";
  position: absolute;
  left: 55%;
  transform: translateX(-50%) scaleY(1) scaleX(1.45);
  top: 180%;
  width: 160%;
  height: 190%;
  background-color: #009087;
  border-radius: 50%;
  display: block;
  transition: all 0.5s 0.1s cubic-bezier(0.55, 0, 0.1, 1);
  z-index: -1;
}

.button2:hover {
  color: #ffffff;
  border: 1px solid #009087;
}

.button2:hover:after {
  top: -45%;
  background-color: #009087;
  transform: translateX(-50%) scaleY(1.3) scaleX(0.8);
}

/* salebadge */
.sale-badge {
  position: absolute;
  background-color: green;
  color: white;
  padding: 7px;
  width: 47px;
  padding: 0.5rem;
  top: 0;
  right: 0;
  border-radius: 0 0 0 1rem;
  animation: attention 2.5s infinite;
}
.sale-badge-soldout {
  padding: 5px;
  animation: attention 2.5s infinite;
  background-color: red;
  color: white;
  padding: 0.5rem;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 0 0 0 1rem;
}
.sale-badge-lessstock {
background-color: orange;
color: white;
animation: attention 2.5s infinite;
          padding: 0.5rem;
          position: absolute;
          top: 0;
          right: 0;
          border-radius: 0 0 0 1rem;
}
@keyframes attention {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
